import type { DemoInstance } from "../../types";

// Import shaders directly as strings
import vertexShaderCode from "./shaders/triangle.vert.wgsl?raw";
import fragmentShaderCode from "./shaders/red.frag.wgsl?raw";

async function loadShaders() {
  return {
    vertex: vertexShaderCode,
    fragment: fragmentShaderCode
  };
}

export async function createHelloTriangleDemo(
  device: GPUDevice,
  format: GPUTextureFormat
): Promise<DemoInstance> {
  const { vertex, fragment } = await loadShaders();

  const pipeline = device.createRenderPipeline({
    layout: "auto",
    vertex: {
      module: device.createShaderModule({ code: vertex }),
      entryPoint: "main"
    },
    fragment: {
      module: device.createShaderModule({ code: fragment }),
      entryPoint: "main",
      targets: [{ format }]
    },
    primitive: {
      topology: "triangle-list"
    }
  });

  return {
    clearColor: { r: 0.1, g: 0.3, b: 0.35, a: 1 },
    requiresDepth: false,
    resize: () => undefined,
    update: () => undefined,
    render: (pass) => {
      pass.setPipeline(pipeline);
      pass.draw(3, 1, 0, 0);
    },
    dispose: () => {
      // GPUShaderModule resources are GC-managed.
    }
  };
}
